body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-poppins: Poppins;
  --font-neue-haas-grotesk-display-pro: "Neue Haas Grotesk Display Pro";

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-mini-4: 14.4px;
  --font-size-mini: 15px;
  --font-size-11xl: 30px;
  --font-size-26xl-1: 45.1px;
  --font-size-6xl: 25px;
  --font-size-11xl-1: 30.1px;
  --font-size-5xl-9: 24.9px;

  /* Colors */
  --color-whitesmoke-100: #f8f8f8;
  --color-whitesmoke-200: #f5f5f5;
  --color-gray-100: #1e1e1e;
  --color-dimgray-100: #4e4e4e;
  --color-black: #000;
  --cool-gray-500: #6b7280;
  --color-darkgray: #9f9f9f;

  /* Gaps */
  --gap-18xl: 37px;
  --gap-145xl: 164px;
  --gap-45xl: 64px;
  --gap-2xl-6: 21.6px;
  --gap-5xl: 24px;
  --gap-3xl: 22px;
  --gap-5xs: 8px;
  --gap-2xs: 11px;
  --gap-8xl: 27px;

  /* Paddings */
  --padding-81xl: 100px;
  --padding-23xl: 42px;
  --padding-46xl: 65px;
  --padding-5xs: 8px;
  --padding-11xl: 30px;
  --padding-2xs: 11px;
  --padding-smi: 13px;
  --padding-13xl: 32px;

  /* Border radiuses */
  --br-8xs: 5px;
}
